@use "global" as *;

.c-card02 {
    display: flex;
    flex-direction: column;
    gap: myrem(20);
    @include mq(md) {
        max-width: myrem(230);
    }
}

.c-card02:nth-child(2) {
    display: flex;
    flex-direction: column;
    gap: myrem(13);
    @include mq(md) {
        max-width: myrem(230);
        gap: myrem(20);
    }
} 

.c-card02__image img{
    aspect-ratio: 345 / 230;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(15);
    @include mq(md) {
        aspect-ratio: 230/300;
        border-radius: myrem(30);
    }
}

.c-card02__wrapper {
    display: flex;
    flex-direction: column;
    @include mq(md) {
        width: myrem(230);
        max-width: myrem(230)
    }
}

.c-card02__title {
    text-align: center;
    font-size: myrem(22);
    line-height: calc(32 / 22);
    letter-spacing: 0.1em;
    font-weight: 400;
    @include mq(md) {
        line-height: calc(30/22);
    }
}

.c-card02__text {
    width: 98%;
    font-size: myrem(14);
    line-height: calc(24 / 14);
    letter-spacing: 0.03em;
    text-align: left;
    margin-top: myrem(9);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    @include mq(md) {
        -webkit-line-clamp: 3;
        width: 101%;
        margin-top: myrem(10);
    }
}


.c-card02__btn {
    width: fit-content;
    margin-left: auto;
    margin-top: myrem(-19);
    margin-right: myrem(-5);
    @include mq(md) {
        margin-top: myrem(-18);
        margin-right: myrem(-7);
    }
}

.c-card02__btn-2 {
    margin-top: myrem(-12);
    @include mq(md) {
        margin-top: myrem(-18);
        margin-right: myrem(-8);
    }
}

.c-card02__btn a{
    width: 100%;
    padding: myrem(13) myrem(6);
    @include mq(md) {
        padding: myrem(8) myrem(8);
    }
}

.c-card02 + .c-card02 {
    margin-top: myrem(20);
    @include mq(md) {
        margin-top: 0;
    }
}