@use "global" as *;

.p-news {
    display: flex;
    flex-direction: column;
    @include mq(md) {
    }
}

.p-news__bg {
    background-image: url("../images/index/sp/newsBg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: myrem(-105);
    @include mq(md) {
        background-image: url("../images/index/pc/newsBg.jpg");
        background-position: bottom;
        margin-top: myrem(-128);
    }
}

.p-news__wrapper {
    padding-top: myrem(244);
    padding-bottom: myrem(75);
    @include mq(md) {
        padding-top: myrem(184);
        padding-bottom: myrem(40);
    }
}

.p-news__content-wrapper {
    @include mq(md) {
        display: flex;
        justify-content: flex-end;
    }
}

.p-news__title {
    @include mq(md) {
        white-space: nowrap;
    }
}

.p-news__wrapper .c-title {
    letter-spacing: 0.04em;
    @include mq(md) {
        letter-spacing: 0.1em;
    }
}

.p-news__cards {
    margin-top: myrem(15);
    @include mq(md) {
        width: myrem(658);
        padding-top: myrem(0);
        padding-left: myrem(34);
        margin-top: 0;
    }
}

.p-news__btn {
    width: fit-content;
    margin-left: auto;
    margin-right: myrem(-7);
    @include mq(md) {
    }
}