@use "global" as *;

//背景が白色のボタン
.c-btn02--w {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    outline: none;
    font-size: myrem(14);
    line-height: calc(20 / 14);
    letter-spacing: 0.1em;
    font-weight: 500;
    border: 1px solid $blue1;
    border-radius: myrem(25);
    color: $blue1;
    background-color: $white;
    padding: myrem(15) myrem(20);
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    z-index: 1;
    @include mq(md) {

    }
}

.c-btn02--w:hover {
    color: #fff;
}

//ホバーしたら背景が流れてくる動き
.c-btn02--w:hover::before {
    transform: scaleX(1);
    transform-origin: left;
}
//ホバーした時の背景
.c-btn02--w::before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: $blue1;
    transform: scaleX(0);
    transform-origin: right;
    transition: all 0.3s ease;
    transition-property: transform;
    z-index: -1;
}

// 矢印の全体
.c-btn02--w span{
    display: inline-flex;
    position: relative;
    @include mq(md) {
    }
}
// 矢印になるパーツ
.c-btn02--w span::before,
.c-btn02--w span::after{
    content: "";
    position: absolute;
    background: $blue1;
    border-radius: .1em;
    rotate: 45deg;
    @include mq(md){

    }
}
// 各々の位置の調整
.c-btn02--w span::before {
    width: 1px;
    height: myrem(9);
    transform: translate(myrem(30), myrem(-38));
    @include mq(md) {
    }
}

.c-btn02--w span::after {
    width: myrem(9);
    height: 1px;
    transform: translate(myrem(26), myrem(-37));
    @include mq(md) {
    }
}

.c-btn02--w:hover span::before,
.c-btn02--w:hover span::after {
    background-color: $white;
}

