@use "global" as *;

.c-card {
    font-size: myrem(14);
    line-height: calc(20 / 14);
    letter-spacing: .03em;
    border-bottom: 1px solid $purple1;
    padding-top: myrem(6);
    padding-bottom: myrem(9);
    display: flex;
    flex-direction: column;
    @include mq(md) {
        flex-direction: row;
        padding-top: myrem(17);
        padding-bottom: myrem(8);
    }
}

.c-card__time {
    white-space: nowrap;
    @include mq(md) {
        padding-right: myrem(15);
    }
}

.c-card__wrapper {
    display: grid;
    grid-template-columns: myrem(60) 1fr;
    gap: myrem(12);
    @include mq(md) {
        width: 80%;
        display: flex;
        gap: myrem(0);
    }
}

.c-card__title {
    @include mq(md) {
        padding-right: myrem(14);
    }
}


.c-card__text {
    width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    @include mq(md) {
        width: 50%;
    }
}

