@use "global" as *;

.p-about {
    display: flex;
    flex-direction: column;
    @include mq(md) {
        display: block;
        position: relative;
    }
}

.p-about__inner {
    @include mq(md) {
        padding-top: myrem(60);
        padding-bottom: myrem(60);
        overflow: hidden;
    }
}

.p-about__wrapper {
    @include mq(md) {
        position: relative;
        display: flex;
        flex-direction: row;
    }
}

.p-about__wrapper::before {
    @include mq(md) {
        position: absolute;
        content: "";
        top: myrem(250);
        left: myrem(330);
        display: inline-block;
        width: calc(526 / 768 * 100vw);
        height: myrem(454);
        background-image: url("../images/index/pc/aboutBg.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -5;
    }
}

.p-about-image-wrapper {
    width: 100%;
    margin-top: myrem(61);
    position: relative;
    height: calc(245 / 375 * 100vw);
    @include mq(md) {
        height: myrem(621);
        margin-top: 0;
        margin-right: auto;
    }
}

.p-about-image-1 {
    position: absolute;
    width: 60.29%;
    z-index: 5;
    @include mq(md) {
        width: myrem(353);
        max-width: myrem(353);
    }
}

.p-about-image-1 img {
    aspect-ratio: 208/245;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(15);
    @include mq(md) {
        aspect-ratio: 353/414;
        border-radius: myrem(30);
        filter: drop-shadow(1px 1px 5px #C4C4C4);
    }
}

.p-about-image-2  {
    position: absolute;
    width: 58.55%;
    top: 62%;
    right: 0;
    @include mq(md) {
        width: myrem(344);
        max-width: myrem(344);
        top: 60%;
        right: auto;
        left: myrem(116);
    }
}

.p-about-image-2 img {
    aspect-ratio: 202/145;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(15);
    @include mq(md) {
        aspect-ratio: 344/246;
        border-radius: myrem(30);
        filter: drop-shadow(1px 1px 5px #C4C4C4);
    }
}

.p-about__text-wrapper {
    margin-top: 15.07%;
    @include mq(md) {
        margin-top: 0;
        position: relative;
        width: myrem(1006);
        padding-top: myrem(95);
        padding-bottom: myrem(186);
    }
}

.p-about__title.c-title {
    padding-top: myrem(28);
    letter-spacing: 0.1em;
    @include mq(md) {
        padding-top: 0;
    }
}

.p-about__text {
    font-size: myrem(14);
    line-height: calc(30/14);
    letter-spacing: 0.03em;
    margin-top: myrem(25);
    @include mq(md) {
        margin-top: myrem(34);
    }
}

.p-about__btn {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: myrem(34);
    padding-bottom: myrem(70);
    @include mq(md) {
        justify-content: end;
        margin-top: myrem(54);
        padding-bottom: 0;
    }
}