@use "global" as * ;

.c-card04 {
    @include mq(md) {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: myrem(41);
    }
}

.c-card04:nth-child(odd) {
    @include mq(md) {
        flex-direction: row-reverse;
    }
}

.c-card04 + .c-card04 {
    margin-top: myrem(28);
    @include mq(md) {
        margin-top: myrem(40);
    }
}

.c-card04__image {
    @include mq(md) {
        width: myrem(437);
        height: myrem(260);
    }

}

.c-card04__image img {
    aspect-ratio: 345/180;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(10);
    @include mq(md) {
        aspect-ratio: 437/260;
        border-radius: myrem(20);
    }
}

.c-card04__wrapper {
    margin-top: myrem(28);
    @include mq(md) {
        margin-top: myrem(10);
        width: 52.3%;
    }
}

.c-card04:nth-child(3) .c-card04__wrapper {
    @include mq(md) {
        margin-top: myrem(3);
    }
}

.c-card04__tag::before {
    counter-increment: number-m 1;
    content: "point " counter(number-m, decimal-leading-zero) "" ;
    font-family: $font-en;
    font-size: myrem(15);
    line-height: calc(18 / 15);
    letter-spacing: 0.1em;
    color: $white;
    background: $blue1;
    padding: myrem(3) myrem(18);
    text-transform: capitalize;
    @include mq(md) {
        font-size: myrem(16);
        padding: myrem(3) myrem(16);
    }
}

.c-card04__title {
    width: 96%;
    font-size: myrem(19);
    line-height: calc(27 / 19);
    letter-spacing: .04em;
    font-weight: normal;
    border-bottom: 1px solid $blue1;
    padding-top: myrem(5);
    padding-bottom: myrem(13);
    @include mq(md) {
        font-size: myrem(24);
        line-height: calc(35 / 24);
        letter-spacing: 0.1em;
        padding-bottom: myrem(6);
    }
}

.c-card04__title::first-letter {
    font-size: myrem(33);
    line-height: calc(48 / 33);
    color: $blue1;
    @include mq(md) {
        font-size: myrem(38);
    }
}

.c-card04__text {
    margin-top: myrem(20);
    font-size: myrem(14);
    line-height: calc(24 / 14);
    letter-spacing: .03em;
    width: calc(314 / 345 * 100%);
    @include mq(md) {
        width: 100%;
        margin-top: myrem(21);
        margin-left: myrem(-1);
    }
}