@use "global" as *;
.p-header {
    z-index: 999;
    background: $white;
    position: fixed;
    height: myrem(60);
    box-sizing: border-box;
    @include mq(md) {
        height: myrem(80);
    }
}

.p-header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    @include mq(md) {
        
    }
}

.p-header__title {
    color: $black;
    font-size: myrem(16);
    line-height: calc(33 / 16);
    letter-spacing: 0.1em;
    font-weight: 100;
    white-space: nowrap;
    padding-left: myrem(15);
    @include mq(md) {
        font-size: clamp( 10px , 28 / 1400 * 100vw , 28px);
        line-height: calc(70 / 28);
        padding-left: clamp(0px , 54.857143 / 768 * 100vw, 100px);
        transition: all 0.5s ease;
    }
}

