@use "global" as *;

.p-contact__bg {
    background-image: url("../images/index/sp/noindexBg.jpg");
    background-position: top;
    width: 100%;
    height: myrem(258);
    position: absolute;
    @include mq(md) {
        
    }
}

.p-contact__inner {
    position: relative;
    padding-top: myrem(40);
    @include mq(md) {
        padding-top: myrem(100);
    }
}

.p-contact__wrapper {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-radius: myrem(20);
    padding: myrem(32) myrem(15) myrem(50);
    @include mq(md) {
        padding: myrem(55) myrem(51) myrem(112);
    }
}

.p-contact__title {
    text-align: center;
}

.p-contact__form-item {
    padding-top: myrem(40);
    display: flex;
    flex-direction: column;
    @include mq(md) {
        padding-top: myrem(30);
    }
}

.p-contact__form-title {
    font-size: myrem(16);
    line-height: calc(21 / 16);
    font-weight: 400;
    letter-spacing: 0.1em;
    @include mq(md) {
        line-height: calc(24/16);
    }
}

.p-contact__form-title.required::before {
    content: "必須";
    display: inline-block;
    font-size: myrem(12);
    line-height: calc(17 / 12);
    font-weight: 400;
    letter-spacing: 0.1em;
    color: $white;
    background-color: #F894B7;
    padding: myrem(6) myrem(9);
    margin-right: myrem(11);
    @include mq(md) {
        padding: myrem(5.5) myrem(9.5);
        margin-right: myrem(12);
    }
}

//各々の余白

.p-contact__form-item--email {
    padding-top: myrem(35);
    @include mq(md) {
        padding-top: myrem(38);
    }
}

.p-contact__form-item--furigana {
    @include mq(md) {
        padding-top: myrem(48);
    }
}

.p-contact__form-item--gender {
    @include mq(md) {
        padding-top: myrem(47);
    }
}

.p-contact__form-item--birthday {
    padding-top: myrem(48);
    @include mq(md) {
        padding-top: myrem(34);
    }
}

.p-contact__form-item--consultation {
    @include mq(md) {
        padding-top: myrem(39);
    }
}

.p-contact__form-item--checkBox {
    padding-top: myrem(47);
    @include mq(md) {
        padding-top: myrem(33);
    }
}

.p-contact__form-item--privacy {
    flex-direction: row;
    justify-content: center;
    padding-top: myrem(13);
    @include mq(md) {
        padding-top: myrem(49);
        
    }
}

.p-contact__form-item--message {
    @include mq(md) {
        padding-top: myrem(41);
        
    }
}

.p-contact__form-item > input ,textarea {
    font-size: myrem(14);
    line-height: calc(18 / 14);
    font-weight: 400;
    letter-spacing: 0.1em;
    border-radius: myrem(10);
    margin-top: myrem(20);
    padding: myrem(10) myrem(23);
    background-color: #EDF5F5;
    border: none;
    resize: none;
    outline: none;
    transition: all 0.3s ease;
    @include mq(md) {
        font-size: myrem(16);
        line-height: calc(24/16);
        border-radius: myrem(20);
        margin-top: myrem(19);
        padding: myrem(20) myrem(24);
        cursor: pointer;
    }
}

input:focus-visible,
textarea:focus-visible {
    outline:none;
}

.p-contact__form-item > input:focus,
.p-contact__form-item > textarea:focus {
    box-shadow: 0px 0px 0px 1px $blue1;
    transition: all 0.3s ease;
}

// プレースホルダー
.p-contact__form-item > input::placeholder {
    color: #CAD5D5;
    font-size: myrem(14);
    line-height: calc(20 / 14);
    letter-spacing: 0.1em;
    @include mq(md) {
        font-size: myrem(16);
        line-height: calc(24/16);
    }
}

.p-contact__form-item--birthday > input::placeholder {
    font-size: myrem(16);
    line-height: calc(24/16);
}


.p-contact__form-item-attention {
    font-size: myrem(12);
    line-height: calc(17/12);
    letter-spacing: 0.1em;
    padding-top: myrem(17);
    @include mq(md) {
        font-size: myrem(14);
        line-height: calc(20/14);
        padding-top: myrem(20);
    }
}

// ラジオボタン
input[type="radio"] {
    display: none;
    @include mq(md) {

    }
}

.p-contact__radio-inner {
    display: flex;
    align-items: center;
    margin-top: myrem(20);
    @include mq(md) {
        margin-top: myrem(14);
    }
}

.p-contact__radio-wrapper {
    position: relative;
    @include mq(md) {
    }
}

.radio__btn {
    display: flex;
    white-space: nowrap;
    font-size: myrem(14);
    line-height: calc(20/14);
    letter-spacing: 0.1em;
    padding-top: myrem(7);
    padding-left: myrem(43);
    padding-right: myrem(38);
    cursor: pointer;
    @include mq(md) {
        padding-top: myrem(14);
        padding-left: myrem(45);
        padding-right: myrem(36);
        padding-bottom: myrem(14);
    }
}

.radio__btn::before {
    content: "";
    display: inline-block;
    border-radius: 50%;
    border: 1px solid #707070;
    width: myrem(35);
    height: myrem(35);
    position: absolute;
    top: myrem(0);
    left: myrem(-2);
    transition: border-color 0.3s ease;
    cursor: pointer;
    @include mq(md) {
        top: myrem(5);
    }
}

/* ラジオボタンが選択されたときのスタイル */
input[type="radio"]:checked + .radio__btn::before {
    border-color: $blue2;
    background-color: $white;
    transition: all 0.3s ease;
}

/* ラジオボタンの選択時に内部の円を表示 */
.radio__btn::after {
    content: "";
    display: block;
    width: myrem(21);
    height: myrem(21);
    border-radius: 50%;
    background-color: $blue2;
    position: absolute;
    opacity: 0;
    transform: translate(-181%, 0%);
    transition: background-color 0.3s ease;
    @include mq(md) {
        transform: translate(-190%, -8%);
    }
}

/* 選択されているラジオボタンの内部スタイル */
input[type="radio"]:checked + .radio__btn::after {
    background-color:  $blue2;
    opacity: 1;
    transition: all 0.3s ease;
}

// ラジオボタンの余白の調整

.radio__gender--man {
    @include mq(md) {
        padding-left: myrem(47);
    }
}

.radio__gender--other {
    padding-left: myrem(46);
    @include mq(md) {
        padding-left: myrem(48);
    }
}

// ラジオボタンの内側の円の調整

.radio__gender--man::after {
    @include mq(md) {
        transform: translate(-199%, -8%);
    }
}

.radio__gender--other::after {
    transform: translate(-194%, 0%);
    @include mq(md) {
        transform: translate(-204%, -8%);
    }
}


// ラジオボタンの余白の調整

.radio__consultation--1 {
    padding-left: myrem(45);
    padding-right: myrem(25);
    @include mq(md) {
        padding-left: myrem(47);
        padding-right: myrem(22);
    }
}

// ラジオボタンの円の調整

.radio__consultation--1::before {
    left: myrem(0);
    @include mq(md) {

    }
}

.radio__consultation--2::before {
    left: myrem(-4);
    @include mq(md) {
        left: myrem(-2);
    }
}

// ラジオボタンの内側の円の調整
.radio__consultation--2::after {
    transform: translate(-189%, 0%);
    @include mq(md) {
        transform: translate(-190%, -8%);
    }
}


// checkbox

.p-contact__checkbox input[type="checkbox"] {
    display: none;
}

.p-contact__checkbox-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: myrem(11) myrem(15);
    margin-top: myrem(20);
    @include mq(md) {
        gap: myrem(20) myrem(23);
        margin-top: myrem(19);
    }
}

.p-contact__checkbox:nth-child(3),
.p-contact__checkbox:nth-child(7) {
    @include mq(md) {
        margin-left: myrem(-3);
    }
}

.p-contact__checkbox-btn {
    position: relative;
    display: block;
    width: myrem(150);
    height: myrem(50);
    background-color: #F4F4F4;
    color: $black;
    border-radius: myrem(5);
    cursor: pointer;
    @include mq(md) {
        width: myrem(205);
        height: myrem(35);
        border-radius: myrem(8);
        transition: all 0.3s ease;
    }
}

.p-contact__checkbox-text {
    position: absolute;
    top: myrem(15);
    left: myrem(15);
    font-size: myrem(15);
    font-weight: 400;
    line-height: calc(21/16);
    @include mq(md) {
        top: myrem(9);
        left: myrem(21);
        font-size: myrem(14);
        line-height: calc(20/14);
        letter-spacing: 0.1em;
    }
}

.p-contact__checkbox-btn:hover {
    @include mq(md) {
        color: $white;
        background-color: $blue2;
    }
}

input[type="checkbox"]:checked +.p-contact__checkbox-btn {
    background-color: $blue2;
    color: white;
    transition: all 0.3s ease;
} 

// 担当医へのメッセージ

.p-contact__form-title.discretion::before{
    content: "任意";
    background-color: #707070;
    color: $white;
    display: inline-block;
    font-size: myrem(12);
    line-height: calc(17 / 12);
    font-weight: 400;
    letter-spacing: 0.1em;
    padding: myrem(6) myrem(11);
    margin-right: myrem(11);
    @include mq(md) {
        padding: myrem(5.5) myrem(9.5);
        margin-right: myrem(12);
    }
}

.p-contact__form-text-detail {
    font-size: myrem(12);
    line-height: calc(17/12);
    letter-spacing: 0.04em;
    font-weight: 400;
    text-align: left;
    padding-top: myrem(11);
    @include mq(md) {
        width: auto;
        font-size: myrem(14);
        line-height: calc(20/14);
        letter-spacing: 0.1em;
        padding-top: myrem(21);
    }
}

.p-contact__message-textarea {
    width: 100%;
    height: myrem(182);
    padding-top: myrem(15);
    padding-bottom: myrem(12);
    @include mq(md) {
        padding-top: myrem(20);
    }
}

.p-contact__form-item > textarea {
    font-size: myrem(12);
    line-height: calc(16 / 12);
    letter-spacing: 0.1em;
    padding: myrem(12);
    @include mq(md) {
        font-size: myrem(14);
        line-height: calc(20/ 14);
        padding: myrem(14);
    }
}

.p-contact__input--privacy {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.p-contact__form-item--privacy-text {
    cursor: pointer;
    display: inline-block;
    position: relative;
    font-size: myrem(12);
    line-height: calc(17/12);
    letter-spacing: 0.04em;
    color: $black;
    margin-top: myrem(-3);
    padding-left: myrem(28);
    @include mq(md) {
        font-size: myrem(14);
        line-height: calc(14/20);
        letter-spacing: 0.1em;
        padding-left: myrem(36);
    }
}

/* チェックボックスの枠 */
.p-contact__form-item--privacy-text::before {
    content: '';
    display: block;
    width:myrem(17);
    height: myrem(17);
    position: absolute;
    top: 62%;
    left: 0;
    transform: translateY(-50%);
    border: 1px solid $black;
    background-color: #F8FAFA;
    @include mq(md) {
        top: 70%;
        left: myrem(-2);
    }
}

  /* チェックのアイコン */
.p-contact__form-item--privacy-text::after {
    content: "";
    display: block;
    width: myrem(15);
    height: myrem(20);
    position: absolute;
    top: myrem(1);
    left: 0;
    transform: rotate(45deg);
    transform-origin: center right;
    border-right: 6px solid $blue1;
    border-bottom: 3px solid $blue1;
    opacity: 0;
    @include mq(md) {
        top: myrem(-3);
    }
}

  /* チェックされた時 */
input[type=checkbox]:checked+.p-contact__form-item--privacy-text::after {
    opacity: 1;
}

.p-contact__form-item--privacy-text > span {
    margin-right: myrem(-4);
    @include mq(md) {

    }
}

.p-contact__form-item--privacy-link {
    position: relative;
    @include mq(md) {

    }
}

.p-contact__form-item--privacy-link::after {
    position: absolute;
    content: "";
    width: 97%;
    top: myrem(21);
    left: 0;
    border-bottom: 1px solid #484848;
    @include mq(md) {
        top: myrem(25);
        left: myrem(2);
    }
}

.p-contact__btn{
    display: flex;
    justify-content: center;
    width: 100%;
    padding-top: myrem(42);
    @include mq(md) {
    }
}

.p-contact__btn > .p-btn {
    width: myrem(240);
    @include mq(md) {
        
    }
