@use "global" as *;

.p-hamburger-icon {
  position: relative;
  width: myrem(60);
  height: myrem(60);
  @include mq(md) {
    display: none;
  }
}

.p-hamburger-icon span {
  width: 35%;
  height: myrem(1);
  position: absolute;
  background-color: $black;
  right: myrem(16);
  transition: .5s;
  border-radius: myrem(50);
}

.p-hamburger-icon span:first-of-type{/* ハンバーガーメニューの1番目の線 */
  top: 40%;
}
.p-hamburger-icon span:nth-of-type(2){/* ハンバーガーメニューの2番目の線 */
  top: 51%;
}
.p-hamburger-icon span:last-of-type{/* ハンバーガーメニューの3番目の線 */
  top: 65%;
}

/* ここから下を追記 */
.p-hamburger-icon.active span:first-of-type{/* ハンバーガーメニューの1番目の線 */
  top: 50%;
  transform: rotate(45deg);
}
.p-hamburger-icon.active span:nth-of-type(2){/* ハンバーガーメニューの2番目の線 */
  opacity: 0;/* 透明にする */
}
.p-hamburger-icon.active span:last-of-type{/* ハンバーガーメニューの3番目の線 */
  top: 50%;
  transform: rotate(-45deg);
}
.p-gnavi.active{
  transform: translateX(0); 
}