@use "global" as *;
.p-dental-clinic-interior__inner {
    padding-top: myrem(68);
    padding-bottom: myrem(72);
    @include mq(md) {
        padding-top: myrem(110);
        padding-bottom: myrem(82);
    }
}

.p-dental-clinic-interior__title {
    text-align: center;
}

.p-dental-clinic-interior__title .c-title,
.p-dental-clinic-interior__title .c-title:after {
    letter-spacing: 0.04em;
    @include mq(md) {
        letter-spacing: 0.1em;
    }
}

.p-dental-clinic-interior__buttonArea {
    @include mq(md) {
        position: relative;
        margin-top: 4%;
        height: myrem(60);
    }
}

// ボタンの背景部分
.swiper-button-prev,
.swiper-button-next {
    width: myrem(40);
    height: myrem(40);
    border-radius: myrem(50);
    background-color: $blue1;
    padding: 0;
    z-index: 10;
    @include mq(md) {
        position: absolute;
        bottom: 0;
        width: myrem(50);
        height: myrem(50);
    }
}

// ボタンの位置
.swiper-button-prev {
    @include mq(md) {
        left: auto;
        right: myrem(260);
    }
}

.swiper-button-next {
    @include mq(md) {
        right: myrem(200);
    }
}

// 元のボタンを消す
.swiper-button-prev::after,
.swiper-button-next::after {
    display:none;
}

// ボタンの矢印
.swiper-buttonPrev::before,
.swiper-buttonPrev::after,
.swiper-buttonNext::before,
.swiper-buttonNext::after {
    content: "";
    position: absolute;
    background: $white;
    border-radius: .1em;
    rotate: 45deg;
    width: myrem(1);
    height: myrem(12);
    opacity: 1;
    @include mq(md) {
        width: myrem(1);
        height: myrem(14);
    }
}

.swiper-buttonPrev::after,
.swiper-buttonNext::after {
    rotate: 135deg;
    @include mq(md) {
    }
}

.swiper-buttonPrev::before {
    top: myrem(-13);
    left: myrem(28);
    transform: translate( myrem(9) , myrem(24));
    @include mq(md) {
        top: myrem(-9);
        left: myrem(33);
        transform: translate( 1000% , 166%);
    }
}

.swiper-buttonPrev::after {
    top: myrem(29);
    left: myrem(41);
    transform: translate( myrem(9) , myrem(24));
    @include mq(md) {
        top: myrem(34);
        left: myrem(46);
        transform: translate( 901% , 165%);
    }
}

.swiper-buttonNext::before {
    top: myrem(-14);
    right: myrem(26);
    transform: translate( myrem(28) , myrem(18));
    @include mq(md) {
        top: myrem(-9);
        right: myrem(32);
        transform: translate( 2850% , 123%);
    }
}

.swiper-buttonNext::after {
    top: myrem(32);
    right: myrem(40);
    transform: translate( myrem(-30) , myrem(0));
    @include mq(md) {
        top: myrem(35);
        right: myrem(45);
        transform: translate( -3052% , 1%);
    }
}
