@use "global" as *;

//fadeIn

.js-fadein {
    opacity: 0;
    transform: translate(0,0);
    transition: all 1.5s ease;

    &.js-fadein-left{
        transform: translate(-30px,0);
    }
    &.js-fadein-right{
        transform: translate(30px,0);
    }
    &.js-fadein-up{
        transform: translate(0,-30px);
    }
    &.js-fadein-bottom{
        transform: translate(0,30px);
    }
    &.js-scrollin{
        opacity: 1;
        transform: translate(0, 0);
    }
}

// ハンバーガーメニューを開いた時にスクロール出来なくする
.fixed {
    height: 100%;
    overflow: hidden;
}