@use "global" as *;

.c-btn {
    display: inline-flex;
    gap: myrem(8);
    align-items: center;
    width: 100%;
    font-size: myrem(14);
    line-height: calc(19 / 14);
    letter-spacing: .1em;
    font-weight: bold;
    white-space: nowrap;
    color: $purple1;
    padding: myrem(11) myrem(10);
    @include mq(md) {
        gap: myrem(5);
        font-size: myrem(16);
        line-height: calc(21 / 16);
        padding: myrem(20) myrem(7);
    }
}

.c-btn__arrow {
    display: inline-flex;
    background-color: $purple1;
    width: myrem(16);
    height: myrem(16);
    border-radius: 50%;
    position: relative;
    @include mq(md) {
        width: myrem(18);
        height: myrem(18);
    }
}

.c-btn__arrow::before,
.c-btn__arrow::after {
    content: "";
    position: absolute;
    background: $white;
    border-radius: .1em;
    rotate: 45deg;
}

.c-btn__arrow::before {
    width: 1px;
    height: myrem(5);
    translate: myrem(8) myrem(7);
    @include mq(md) {
        height: myrem(5);
        translate: myrem(9) myrem(8);  
    }
}

.c-btn__arrow::after {
    width: myrem(5);
    height: 1px;
    translate: myrem(6) myrem(6);
    @include mq(md) {
        width: myrem(5);
        translate: myrem(7) myrem(7);
    }
}

// treatmentdetailes

.c-btn--g {
    color: $blue1;
    font-size: myrem(16);
    line-height: calc(20 / 16);
    gap: myrem(6);
}

.c-btn--g__arrow {
    background: $blue1;
    width: myrem(18);
    height: myrem(18);
}

.c-btn--g__arrow::before {
    translate: myrem(9) myrem(9)
}

.c-btn--g__arrow::after {
    translate: myrem(7) myrem(7)
}