@use "global" as *;

.c-title {
    font-size: myrem(28);
    letter-spacing: 0.04em;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    @include mq(md) {
        font-size: myrem(32);
        letter-spacing: 0.1em;
    }
}

.c-title::after {
    content: attr(data-en);
    font-size: myrem(14);
    line-height: calc(20 / 14);
    letter-spacing: 0.04em;
    font-weight: 400;
    color: $blue1;
    text-transform: capitalize;
    @include mq(md) {
        font-size: myrem(18);
        line-height: calc(26/18);
        letter-spacing: 0.1em;
    }
}

.c-title--p:after {
    color: $purple1;    
}