@use "global" as *;

.p-footer__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: myrem(80);
    background-color: #F8FFFF;
    @include mq(md) {
        padding-top: myrem(74);
    }
}

.p-footer__linkWrapper {
    @include mq(md) {
        display: flex;
        flex-direction:row;
    }
}

.p-footer__title {
    font-size: myrem(22);
    line-height: calc(33/22);
    letter-spacing: 0.1em;
    font-weight: 100;
    text-align: center;
    padding-bottom: myrem(14);
    @include mq(md) {
        display: inline-block;
        font-size: myrem(19);
        line-height: calc(29/19);
        text-align: left;
        white-space: nowrap;
        padding-bottom: 0;
    }
}

.p-footer__title a {
    padding: myrem(5);
    @include mq(md) {
        padding: myrem(20) myrem(10);
        margin-left: myrem(-12);
    }
}


.p-footer__list {
    display: flex;
    flex-direction: column;
    width: 100%;
    @include mq(md) {
        flex-direction: row;
        width: auto;
        padding-left: myrem(78);
    }
}

.p-footer__item {
    @include mq(md) {
        padding-left: myrem(35);
    }
}

.p-footer__item a {
    display: block;
    width: 100%;
    height: 100%;
    padding-top: myrem(22);
    @include mq(md) {
        display: inline;
        padding-top: myrem(1);
    }
}

.p-footer__item:nth-child(2) a,
.p-footer__item:nth-child(4) a {
    padding-top: myrem(20);
    @include mq(md) {
        padding-top: myrem(1);
    }
}

.p-footer__item:nth-child(3) a,
.p-footer__item:nth-child(5) a {
    padding-top: myrem(22);
    @include mq(md) {
        padding-top: myrem(1);
    }
}

.p-footer__item > a > p {
    font-size: myrem(16);
    letter-spacing: 0.04em;
    font-weight: 400;
    padding-bottom: myrem(4);
    border-bottom: 1px solid $blue2;
    @include mq(md) {
        letter-spacing: 0.1em;
        white-space: nowrap;
        padding-bottom: myrem(5);
    }
}

.p-footer__item ul {
    margin: 0 myrem(14);
    @include mq(md) {
        margin: 0;
        margin-left: myrem(1);
    }
}

.p-footer__accordion {
    width: 100%;
    height: 100%;
    border-bottom: 1px solid  #E0E0E0;
    @include mq(md) {
        border-bottom: 0;
    }
}

.p-footer__accordion a {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: myrem(14);
    letter-spacing: 0.1em;
    font-weight: 400;
    white-space: nowrap;
    padding-top: myrem(18);
    padding-bottom: myrem(5);
    @include mq(md) {
        padding-top: myrem(0);
        padding-bottom: myrem(9);
        padding-left: myrem(3);
        border-bottom: 0;
    }
}

.p-footer__accordion:nth-child(1) a {
    padding-top: myrem(18);
    @include mq(md) {
        padding-top: myrem(11);
    }
}

.p-footer__accordion span {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    @include mq(md) {
        
    }
}

.p-footer__accordion span::before {
    content: '';
    width: myrem(6);
    height: myrem(6);
    border: 0;
    border-top: solid 1px #484848;
    border-right: solid 1px #484848;
    transform: rotate(45deg);
    position: absolute;
    top: myrem(-6);
    right: myrem(3);
    margin: auto;
    @include mq(md) {
        top: myrem(-2);
        right: myrem(3);
    }
}

.p-footer__copyright {
    text-align: center;
    font-size: myrem(12);
    letter-spacing: 0.04em;
    font-weight: 400;
    padding-top: myrem(51);
    padding-bottom: myrem(45);
    @include mq(md) {
        letter-spacing: 0.1em;
        padding-top: myrem(65);
        padding-bottom: myrem(37);
    }
}
