@use "global" as *;

.p-about-us-greeting__bg {
    background-image: url("../images/index/sp/noindexBg.jpg");
    background-position: top;
    width: 100%;
    height: myrem(258);
    position: absolute;
    @include mq(md) {
        background-image: url("../images/about/pc/noindexBg.jpg");
    }
}

.p-about-us-greeting__inner {
    position: relative;
    padding: myrem(40) myrem(15) 0;
    @include mq(md) {
        padding: 0;
        padding-top: myrem(100);
        display: flex;
        justify-content: center;
    }
}

.p-about-us-greeting__wrapper {
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: #fff;
    border-radius: myrem(20);
    padding: myrem(20) myrem(15) myrem(80) myrem(15);
    @include mq(md) {
        width: clamp( 750px , 1200 / 1400 * 100vw , 1200px);
        max-width: 1200px;
        padding: myrem(31) myrem(100) myrem(110);
    }
}

.p-about-us-greeting__bread-crumbs {
    display: flex;
    align-items: center;
    padding-left: myrem(3);
    position: relative;
    @include mq(md) {
        padding-left: myrem(0);
    }
}

.p-about-us-greeting__bread-crumbs::before {
    content: "";
    width: myrem(8);
    height: myrem(8);
    border: 0;
    border-top: solid 1px $black;
    border-right: solid 1px $black;
    transform: rotate(45deg);
    position: absolute;
    top: myrem(6);
    left: myrem(36);
    margin: auto;
    @include mq(md) {
        top: myrem(10);
        left: myrem(42);
    }
}

.p-about-us-greeting__bread-crumb-text {
    font-size: myrem(12);
    letter-spacing: 0.03em;
    @include mq(md) {
        font-size: myrem(16);
    }
}

.p-about-us-greeting__bread-crumb-text:nth-child(2) {
    padding-left: myrem(28);
    @include mq(md) {
        padding-left: myrem(32);
    }
}

.p-about-us-greeting__title {
    text-align: center;
    padding-top: myrem(31);
    @include mq(md) {
        padding-top: myrem(39);
    }
}

.p-about-us-greeting__introduce-wrapper {
    display: flex;
    flex-direction: column;
    padding-top: myrem(25);
    @include mq(md) {
        flex-direction: row;
        justify-content: space-between;
        padding-top: myrem(40);
    }
}

.p-about-us-greeting__introduce-image {
    width: 100%;
    @include mq(md) {
        width: 48.5%;
        max-width: 485px;
        max-height: 290px;
    }
}

.p-about-us-greeting__introduce-image img{
    aspect-ratio: 315/188;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(20);
    @include mq(md) {
        aspect-ratio: 485/290;
    }
}

.p-about-us-greeting__introduce-text-contents {
    display: flex;
    flex-direction: column;
    padding-top: myrem(14);
    font-size: myrem(14);
    line-height: calc(30 / 14);
    font-weight: 400;
    text-align: left;
    @include mq(md) {
        width: 48.5%;
        max-width: 485px;
        font-size: clamp( 10px , 14 / 1400 * 100vw , 14px);
        padding-top: myrem(49);
        text-align: initial;
    }
}

.p-about-us-greeting__introduce-text {
    @include mq(md) {
        padding-right: myrem(7.5);
    }
}

.p-about-us-greeting__introduce-name {
    display: flex;
    align-items: baseline;
    justify-content: end;
    font-size: myrem(22);
    line-height: calc(30 / 22);
    font-weight: 400;
    letter-spacing: 0.03em;
    padding-top: myrem(16);
    padding-right: myrem(4);
    @include mq(md) {
        font-size: clamp( 10px , 22 / 1400 * 100vw , 22px);
        padding-top: myrem(19);
        padding-right: myrem(5);
    }
}

.p-about-us-greeting__introduce-name span {
    font-size: myrem(14);
    line-height: calc(30 / 14);
    font-weight: 400;
    letter-spacing: 0.03em;
    padding-right: myrem(15);
    @include mq(md) {

    }

}


.p-about-us-greeting__card-wrapper {
    display: flex;
    flex-direction: column;
    text-align: left;
    padding-top: myrem(43);
    @include mq(md) {
        flex-direction: row;
        justify-content: space-between;
        padding-top: myrem(46);
    }
}

.p-about-us-greeting__card-item--career-content {
    @include mq(md) {
        width: 48.5%;
        max-width: 485px;
    }
}

.p-about-us-greeting__card-item--career-title {
    padding-bottom: myrem(4);
    border-bottom: 2px solid $blue1;
    @include mq(md) {
        padding-bottom: myrem(8);
    }
}

.p-about-us-greeting__card-item--career-title span {
    color: $white;
    background-color: $blue1;
    border-radius: myrem(8) myrem(8) 0 0;
    padding: myrem(8) myrem(35) myrem(6);
    font-size: myrem(18);
    letter-spacing: 0.1em;
    @include mq(md) {
        font-size: myrem(22);
        padding: myrem(8) myrem(35) myrem(10);
    }
}

.p-about-us-greeting__card-item--career-card {
    font-size: myrem(14);
    line-height: calc(24/14);
    font-weight: 400;
    letter-spacing: 0.08em;
    padding: myrem(10) 0 myrem(13);
    border-bottom: 1px solid $blue1;
    @include mq(md) {
        padding: myrem(17) 0;
    }
}

.p-about-us-greeting__card-item--career-card:nth-child(2) {
    padding: myrem(12) 0;
    @include mq(md) {
        padding: myrem(18) 0;
    }
}

.p-about-us-greeting__card-item--career-card:nth-child(3) {
    padding: myrem(13) 0;
    @include mq(md) {
        padding: myrem(17) 0;
    }
}

.p-about-us-greeting__card-item--career-card:nth-child(4) {
    padding: myrem(13) 0;
    @include mq(md) {
        padding: myrem(19) 0;
    }
}

.p-about-us-greeting__card-item--career-card-text-space {
    display: none;
    @include mq(md) {
        display: inline;
    }
}

.p-about-us-greeting__card-item--affiliation-qualification--content {
    padding-top: myrem(34);
    @include mq(md) {
        width: 48.5%;
        max-width: 485px;
        padding-top: 0;
    }
}

.p-about-us-greeting__card-item--affiliation-qualification-content-title {
    padding-bottom: myrem(6);
    border-bottom: 2px solid $blue1;
    @include mq(md) {

    }
}

.p-about-us-greeting__card-item--affiliation-qualification-content-title span {
    color: $white;
    background-color: $blue1;
    border-radius: myrem(8) myrem(8) 0 0;
    padding: myrem(6) myrem(13) myrem(7) myrem(16);
    font-size: myrem(18);
    letter-spacing: 0.1em;
    @include mq(md) {
        font-size: myrem(22);
        padding: myrem(8) myrem(12) myrem(9) myrem(15);
    }
}

.p-about-us-greeting__card-item--affiliation-content,
.p-about-us-greeting__card-item--qualification-content {
    font-size: myrem(14);
    font-weight: 400;
    letter-spacing: 0.08em;
    @include mq(md) {

    }
}

.p-about-us-greeting__card-item--affiliation-content {
    padding-top: myrem(14);
    padding-bottom: myrem(8);
    border-bottom: 1px solid $blue1;
    @include mq(md) {
        display: flex;
        align-items: flex-start;
        padding-top: myrem(19);
        padding-bottom: myrem(14);
    }
}

.p-about-us-greeting__card-item--affiliation-title {
    padding-bottom: myrem(7);
    @include mq(md) {
        padding-right: myrem(53);
        padding-bottom: 0;
    }
}

.p-about-us-greeting__card-item--affiliation-cards,
.p-about-us-greeting__card-item--qualification-cards  {
    list-style:  disc;      
    list-style-position: inside;
    margin:  0;             /* デフォルト指定上書き */
    padding: 0;             /* デフォルト指定上書き */
    @include mq(md) {

    }
}

.p-about-us-greeting__card-item--affiliation-card,
.p-about-us-greeting__card-item--qualification-card {
    position: relative;
    padding-left: myrem(5);
    padding-bottom: myrem(4);
    @include mq(md) {
        padding-left: 0;
    }
}

::marker{
    color: $black;
    font-family: $font-main;
    font-size: myrem(7);
    font-weight: 400;
}

.p-about-us-greeting__card-item--affiliation-card span{
    text-decoration: underline;
    text-underline-offset: myrem(2);
    @include mq(md) {
        
    }
}

.p-about-us-greeting__card-item--qualification-content {
    padding-top: myrem(20);
    padding-bottom: myrem(9);
    border-bottom: 1px solid $blue1;
    @include mq(md) {
        display: flex;
        align-items: flex-start;
        padding-top: myrem(19);
        padding-bottom: myrem(14);
    }
}

.p-about-us-greeting__card-item--qualification-title {
    padding-bottom: myrem(9);
    @include mq(md) {
        padding-right: myrem(22);
        padding-bottom: 0;
    }
}

// facility
.p-about-us-facility {
    position: relative;
    @include mq(md) {

    }
}

.p-about-us-facility__bg {
    position: absolute;
    right: 31.2%;
    width: 100%;
    height: 100%;
    z-index: -1;
    @include mq(md) {
        right: 36.07%;
    }
}

.p-about-us-facility__bg img {
    width: 100%;
    height: 100%;
    aspect-ratio: 516/4590;
    object-fit: cover;
    @include mq(md) {
        aspect-ratio: 1790/2260;
    }
}

.p-about-us-facility__title {
    text-align: center;
    padding-top: myrem(57);
    @include mq(md) {
        padding-top: myrem(99);
    }
}

.p-about-us-facility__wrapper {
    display: flex;
    flex-direction: column;
    padding-top: myrem(40);
    padding-bottom: myrem(40);
    @include mq(md) {
        flex-direction: row;
        flex-wrap: wrap;
        padding: myrem(38) myrem(25) myrem(110);
    }
}

.p-about-us-facility__wrapper .c-card05 {
    padding-top: myrem(36);
    @include mq(md) {
        padding-top: myrem(0);
    }
}

.p-about-us-facility__wrapper .c-card05:nth-child(1) {
    padding-top: 0;
    @include mq(md) {

    }
}

// overview

.p-about-us-company-overview__wrapper {
    padding: myrem(75) 0;
    @include mq(md) {
        padding: myrem(106) 0;
    }
}

.p-about-us-company-overview__title {
    text-align: center;
}

.p-about-us-company-overview__list {
    width: 100%;
    padding-top: myrem(35);
    @include mq(md) {
        display: flex;
        flex-wrap: wrap;
        padding-top: myrem(40);
    }
}

.p-about-us-company-overview__listTitle {
    font-size: myrem(14);
    line-height: calc(19 / 14);
    letter-spacing: 0.1em;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: myrem(10) 0;
    background-color: #EDF5F5;
    box-shadow: inset 0px 1px 0px 0px rgba($color: #000, $alpha: 0.31);
    @include mq(md) {
        font-size: myrem(16);
        line-height: calc(24 / 16);
        width: myrem(314);
        padding: myrem(20) 0;
    }
}

.p-about-us-company-overview__listContent {
    font-size: myrem(14);
    line-height: calc(20 / 14);
    letter-spacing: 0.1em;
    text-align: center;
    padding: myrem(10) myrem(15) myrem(20);
    @include mq(md) {
        text-align: left;
        font-size: myrem(16);
        line-height: calc(24 / 16);
        padding: myrem(20) myrem(28);
        width: myrem(686);
        box-shadow: inset 0px 1px 0px 0px rgba($color: #000, $alpha: 0.31);
    }
}

.p-about-us-company-overview__listTitle:nth-child(9) {
    @include mq(md) {
        padding: myrem(19) 0;
    }
}

.p-about-us-company-overview__listTitle:nth-child(13) {
    @include mq(md) {
        border-bottom: 1px solid rgba($color: #000, $alpha: 0.31);
    }
}

.p-about-us-company-overview__listContent:nth-child(6) {
    @include mq(md) {
        padding: myrem(19) myrem(28) myrem(17);
    }
}

.p-about-us-company-overview__listContent:nth-child(10) {

    @include mq(md) {
        padding: myrem(19) myrem(28) myrem(17);
    }
}

.p-about-us-company-overview__listContent:nth-child(14) {
    padding-bottom: myrem(10);
    border-bottom: 1px solid rgba($color: #000, $alpha: 0.31);
    @include mq(md) {
        padding: myrem(19) myrem(28) myrem(17);
    }
}

.p-about-us-company-overview__listContent  a {
    text-decoration: underline;
    @include mq(md) {

    }
}

