@use "global" as *;

.p-contacted {
    position: relative;
    @include mq(md) {

    }
}

.p-contacted__bg {
    background-image: url("../images/index/sp/noindexBg.jpg");
    background-position: top;
    width: 100%;
    height: myrem(258);
    position: absolute;
    @include mq(md) {
        background-image: url("../images/about/pc/noindexBg.jpg");
    }
}

.p-contacted__inner {
    position: relative;
    padding-top: myrem(40);
    padding-right: myrem(15);
    padding-left: myrem(15);
    @include mq(md) {
        padding-top: myrem(100);
        display: flex;
        justify-content: center;
        padding-right: myrem(0);
        padding-left: myrem(0);
    }
}

.p-contacted__wrapper {
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: #fff;
    border-radius: myrem(20);
    padding: myrem(46) myrem(15) myrem(0) myrem(15);
    @include mq(md) {
        width: myrem(1000);
        max-width: 1000px;
        padding: 0;
        padding-top: myrem(70);
        margin: 0 auto;
    }
}

.p-contacted__wrapper > h2 {
    font-size: myrem(24);
    line-height: calc(35 / 24);
    font-weight: 400;
    letter-spacing: 0.1em;
    position: relative;
    padding-bottom: myrem(8);
    @include mq(md) {
        font-size: myrem(32);
        line-height: calc(46 / 32);
        padding-bottom: myrem(18);
        margin-left: myrem(-12);
    }
}

.p-contacted__wrapper > h2::before {
    content: "";
    position: absolute;
    width: myrem(55);
    height: myrem(4);
    background: $blue1;
    top: 21vw;
    left: 34.7vw;
    @include mq(md) {
        top: myrem(61);
        left: myrem(476);
    }
}

.p-contacted__wrapper > p {
    font-size: myrem(14);
    line-height: calc(29 / 14);
    font-weight: 400;
    letter-spacing: 0.03em;
    text-indent: 0.03em;
    padding-top: myrem(21);
    @include mq(md) {
        padding-top: myrem(28);
        line-height: calc(34 / 14);
        letter-spacing: 0.1em;
    }
}

.p-contacted__btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: myrem(32);
    padding-bottom: myrem(61);
    @include mq(md) {
        padding-top: myrem(52);
        padding-bottom: myrem(110);
    }
}