@use 'global' as *;

.p-floating__area {
    position: relative;
}

.p-floating__menuInner {
    display: none;
    position: fixed;
    width: 100%;
    bottom: 0;
    z-index: 99;
    @include mq(md) {
        display: none;
    }
}

.p-floating__menuWrapper {
    display: flex;
    align-items: center;
    @include mq(md) {
        display: none;
    }

}

// 電話の部分

.p-floating__tellWrapper {
    width: 50%;
    height: myrem(60);
    background-color: $white;
}

.p-floating__tellLink {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: myrem(17) 0 myrem(16);
    gap: myrem(6.5);
}

.p-floating__tellImage {
    width: myrem(11);
    height: myrem(16);
}

.p-floating__tellImage img {
    aspect-ratio: 11 / 16;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-floating__tellText {
    font-size: myrem(19);
    line-height: calc(27 / 17);
    letter-spacing: 0.027em;
    font-weight: bold;
    color: #707070;
}

// webの部分

.p-floating__webWrapper {
    width: 50%;
    height: myrem(60);
    background-color: $blue1;
}

.p-floating__webLink {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: myrem(17) 0 myrem(18);
    gap: myrem(14.5);
}

.p-floating__webImage {
    width: myrem(23);
    height: myrem(24);
}

.p-floating__webImage img {
    aspect-ratio: 23 / 24;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-floating__webText {
    font-size: myrem(17);
    line-height: calc(25 / 17);
    letter-spacing: 0.1em;
    color: $white;
}

/*フッターまでスクロールしたら消す*/
.js-floating__hidden{
    visibility: hidden;
    opacity: 0;
}