@use "global" as * ;

.p-message__inner {
    position: relative;
    overflow: hidden;
    @include mq(md) {
    }
}

.p-message__wrapper {
    width: 100%;
    background-image: url("../images/index/sp/messageBg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: -31.2vw;
    background-position-y: myrem(193);
    z-index: -1;
    @include mq(md) {
        background-image: url("../images/index/pc/messageBg.jpg");
        background-position-x: -36.071429vw;
        background-position-y: myrem(180);
    }
}

.p-message__title {
    padding-left: myrem(15);
    @include mq(md) {
        text-align: initial;
        padding-top: myrem(73);
        padding-left: 0;
    }
}

.p-message__title .c-title,
.p-message__title .c-title:after {
    letter-spacing: 0.04em;
    @include mq(md) {
        text-align: initial;
        letter-spacing: 0.1em;
    }
}

.p-message__card-inner {
    padding-top: myrem(25);
    @include mq(md) {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: myrem(36);
    }
}

.p-message__card {
    position: relative;
    @include mq(md) {
        display: flex;
        width: 100%;
        max-width: 1400px;
    }
}

.p-message__card-image {
    width: 96vw;
    @include mq(md) {
        width:clamp(362px , 660 / 1400 * 100vw , 660px);
    }
}

.p-message__card-image img {
    aspect-ratio: 360/250;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0 myrem(30) myrem(30) 0;
    @include mq(md) {
        aspect-ratio: 660/380;
        border-radius: 0 myrem(50) myrem(50) 0;
    }
}

.p-message__card-image-2 {
    position: absolute;
    width: 60.26%;
    top: myrem(0);
    right: myrem(16);
    @include mq(md) {
        width: clamp(20px , 238 / 1400 * 100vw , 238px) ;
        height: clamp(20px , 74 / 1400 * 100vw , 74px);
        top: myrem(66);
        right: clamp(105px , 245 / 1400 * 100vw , 245px);
    }
}

.p-message__card-image-2 img {
    aspect-ratio: 213/67;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include mq(md) {
        aspect-ratio: 238/74;
    }
}

.p-message__card-wrapper {
    padding-top: myrem(13);
    @include mq(md) {
        display: flex;
        flex-direction: column;
        width: myrem(540);
        padding-top: 0;
        padding-left: myrem(39);
    }
}

.p-message__card-text {
    font-size: myrem(14);
    line-height: calc(24 / 14);
    letter-spacing: 0.03em;
    padding-left: myrem(15);
    @include mq(md) {
        margin-top: myrem(36);
    }
}

.p-message__card-wrapper > .l-inner {
    @include mq(md) {
        padding: 0;
    }
}

.p-message__wrapper .p-card04 {
    margin-top: myrem(45);
    padding-bottom: myrem(70);
    @include mq(md) {
        margin-top: myrem(40);
        padding-bottom: myrem(138);
    }
}