@use "global" as *;

.p-access {
    display: flex;
    flex-direction: column;
    position: relative;
    @include mq(md) {

    }
}

.p-access__bg {
    width: 100%;
    background-image: url("../images/index/sp/accessBg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: myrem(193);
    @include mq(md) {
        background-position-y: myrem(384);
    }
}

.p-access__title {
    text-align: center;
    @include mq(md) {
        margin-top: myrem(-2);

    }
}

.p-access__wrapper  .c-title,
.p-access__wrapper  .c-title:after {
    letter-spacing: 0.04em;
    @include mq(md) {
        letter-spacing: 0.1em;
    }
}

.p-access__map-image {
    margin-top: myrem(30);
    @include mq(md) {
        margin-top: myrem(40);
    }
}

.p-access__map-image iframe {
    aspect-ratio: 345 / 195;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(50);
    @include mq(md) {
        aspect-ratio: 1000/499;

    }
}

.p-access__text-wrapper {
    margin-top: myrem(16);
    @include mq(md) {
        display: flex;
        justify-content: space-between;
        margin-top: myrem(61);
    }
}

.p-access__text-title {
    font-size: myrem(24);
    font-weight: 100;
    letter-spacing: 0.1em;
    @include mq(md) {
        font-size: myrem(32);
        margin-top: myrem(-5);
    }
}

.p-access__tell-number {
    display: flex;
    align-items: baseline;
    font-size: myrem(30);
    font-weight: 700;
    margin-top: myrem(11);
    margin-left: myrem(-1);
    @include mq(md) {
        font-size: myrem(35);
        margin-top: myrem(19);
    }
}

.p-access__tell-number::before {
    content: "TEL.";
    font-size: myrem(19);
    @include mq(md) {
        font-size: myrem(22);
    }
}

.p-access__text {
    font-size: myrem(12);
    line-height: calc(24 / 12);
    margin-left: myrem(-1);
    @include mq(md) {
        font-size: myrem(14);
        line-height: calc(32/14);
    }
}

.p-access__address {
    margin-top: myrem(7);
    @include mq(md) {
        margin-top: myrem(12);
    }
}

// カレンダー

.p-access__schedule-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: myrem(35);
    @include mq(md) {
        width: myrem(500);
        max-width: 500px;
        margin-top: myrem(79);
    }
}

// カレンダーの枠

.p-access__schedule  tr {
    border-top: 1px solid $black;
    @include mq(md) {

    }
}

.p-access__schedule  tbody > tr:nth-child(2) {
    border-bottom: 1px solid $black;
    @include mq(md) {

    }
}

// カレンダー要素の幅

.p-access__schedule-table--blank {
    width: 6.4%;
    font-size: 0;
    @include mq(md) {
    }
}

.p-access__schedule-table--narrow {
    width: 3.4%;
    padding-right: myrem(4);
    @include mq(md) {

    }
}

.p-access__schedule-table--wide {
    width: 6%;
    @include mq(md) {

    }
}

// カレンダー要素のdetail
.p-access__schedule-table--thead {
    font-size: myrem(14);
    letter-spacing: -0.05em;
    font-weight: 400;
    padding-top: myrem(9);
    padding-bottom: myrem(9);
    @include mq(md) {
        font-size: myrem(16);
        letter-spacing: 0.1em;
        padding-top: myrem(5);
        padding-bottom: myrem(5);
    }
}

.p-access__schedule-table--tbody-title {
    font-size: myrem(14);
    line-height: calc(20/14);
    letter-spacing: 0;
    font-weight: 400;
    white-space: wrap;
    padding-top: myrem(9);
    padding-bottom: myrem(7);
    @include mq(md) {
        font-size: myrem(16);
        line-height: 0;
        padding-top: myrem(27);
        padding-bottom: myrem(23);
    }
}

.p-access__schedule-table--tbody-title-2 {
    padding-top: myrem(10);
    @include mq(md) {
        padding-top: myrem(25);
        padding-bottom: myrem(24);
    }
}

.p-access__schedule-table--tbody-td {
    font-size: myrem(16);
    letter-spacing: -0.17em;
    font-weight: 400;
    text-align: center;
    @include mq(md) {
        font-size: myrem(16);
        letter-spacing: 0.1em;
    }
}

.p-access__btn {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: myrem(39);
    padding-bottom: myrem(63);
    @include mq(md) {
        margin-top: myrem(43);
        padding-bottom: myrem(112);
    }
}