@use "global" as *;

//headerの分mainを下げる
main {
    padding-top: myrem(60);
    @include mq(md) {
        padding-top: myrem(80);
    }
}

.p-fv {
    position: relative;
    display: flex;
    flex-direction: column;
    @include mq(md) {

    }
}

.p-fv__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.p-fv__image {
    width: 83.73%;
    @include mq(md) {
        width: 71.4%;
    }
}

.p-fv__image img {
    aspect-ratio: 314/232;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(30) 0 myrem(30) myrem(30);
    @include mq(md) {
        aspect-ratio: 1000/593;
        border-radius: myrem(5) 0 0 myrem(50);
    }
}

.p-fv__catch-copy-inner {
    position: absolute;
    top: 16vw;
    left: myrem(15);
    @include mq(md) {
        top: 5.714vw;
        left: 7.142vw;
    }
}

.p-fv__catch-copy-wrapper {
    position: relative;
    width: 100%;
    @include mq (md) {

    }
}

.p-fv__catch-copy-en {
    position: absolute;
    font-family: $font-sub;
    font-size: 4.8vw;
    color: $blue1;
    top: -8vw;
    left: 1.6vw;
    transform: rotate(-7deg);
    @include mq(md) {
        font-size: 3.785vw;
        top: 0;
        left: 1.285vw;
    }
}

.p-fv__catch-copy-1 , 
.p-fv__catch-copy-2 {
    position: absolute;
    font-size: myrem(22);
    font-weight: 400;
    letter-spacing: 0.1em;
    color: $white;
    background-color: $blue2;
    top: myrem(154);
    padding-top: myrem(5);
    padding-bottom: myrem(2);
    padding-left: myrem(6);
    padding-right: myrem(20);
    white-space: nowrap;
    @include mq(md) {
        top: 7.642vw;
        left: 7.142vw;
        font-size: 2.428vw;
        padding-top: myrem(3);
        padding-right: myrem(6);
        padding-bottom: myrem(6);
    }
}

.p-fv__catch-copy-2 {
    top: myrem(201);
    padding-right: myrem(21);
    padding-top: myrem(3);
    padding-bottom: myrem(6);
    @include mq(md) {
        top: 12.57vw;
        padding-right: myrem(7);
    }
}

.p-fv__catch-copy-3 {
    position: absolute;
    top: myrem(253);
    font-size: myrem(15);
    font-weight: 400;
    line-height: calc(21/15);
    letter-spacing: 0.035em;
    white-space: nowrap;
    @include mq(md) {
        top: 18vw;
        left: 7.142vw;
        font-size: 1.428vw;
        line-height: calc(27 / 20);
    }
}