@use "global" as *;

.p-treatment-details__inner {
    padding-top: myrem(66);
    padding-bottom: myrem(68);
    @include mq(md) {
        padding-top: myrem(72);
        padding-bottom: myrem(68);
    }
}

.p-treatment-details__bg {
    background-image: url("../images/index/sp/treatmentDetailsBg.jpg");
    background-size: 100% 186.66vw;
    background-repeat: no-repeat;
    background-position: top;
    @include mq(md) {
        background-image: url("../images/index/pc/treatmentDetailsBg.jpg");
        background-size: 100% myrem(400);
    }
}

.p-treatment-details__title {
    text-align: center;
    @include mq(md) {

    }
}

.p-treatment-details__title.c-title,
.p-treatment-details__title .c-title:after {
    letter-spacing: 0.04em;
    @include mq(md) {
        letter-spacing: 0.1em;
    }
}

.p-treatment-details__card-wrapper {
    display: flex;
    flex-direction: column;
    margin-top: myrem(30);
    @include mq(md) {
        flex-direction: row;
        justify-content: space-between;
        margin-top: myrem(39);
    }
}

.p-treatment-details__btn {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: myrem(27);
    @include mq(md) {
        margin-top: myrem(55);
    }
}
