@use "global" as *;

.p-flow {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: myrem(68);
    @include mq(md) {
        flex-direction: row;
        padding-bottom: myrem(77);
    }
}

.p-flow__title {
    @include mq(md) {
        text-align: left;
        margin-top: myrem(-72);
    }
}

.p-flow > .p-card03{
    margin-top: myrem(30);
    @include mq(md) {
        display: flex;
        flex-direction: row;
        margin-top: myrem(45);
        margin-left: myrem(20);
    }
}

.p-flow > .p-card03 > .c-card03:nth-child(1)::before {
    width: 0;
    height: 0;
    @include mq(md) {

    }
}

.p-flow > .p-card03 >.c-card03:nth-child(2) {
    @include mq(md) {
        margin-left: myrem(26);
    }
}

.p-flow > .p-card03 >.c-card03:nth-child(3) {
    margin-left: 4vw;
    @include mq(md) {
        margin-left: myrem(29);
    }
}

.p-flow > .p-card03 >.c-card03:nth-child(4) {
    @include mq(md) {
        margin-left: myrem(27);
    }
}

.p-flow .c-title {
    letter-spacing: 0.1em;
    @include mq(md) {
    }
}