@use "global" as *;

.p-dental-clinic-interior__swiper {
  margin-top: myrem(27);
  @include mq(md) {
      margin-top: myrem(27);
  }
}

.swiper-wrapper {
  @include mq(md) {
    align-items: center;
    margin-top: 3.3571429%;
    padding-left: 1.2857143%;
    margin-left: -3.2142857%;
  }
}

.swiper-slide {
  width: 65.06667%;
  padding: myrem(5);
  @include mq(md) {
    width: auto;
    transform: scale(1);
    transition: all 0.5s ease;
    padding: 0;
    padding-right: 1.4285714%;
  }
}  


.swiper__slide-img img {
  aspect-ratio: 244/239;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: myrem(20);
  @include mq(md) {
      aspect-ratio: 356/346;
  }
}

.swiper-slide-active {
  @include mq(md) {
    transform: scale(1.2116992);
    margin-left: 3.2142857%;
    margin-right: 2.5%;
  }
}
