@use "global" as *;

.c-card05 {
    @include mq(md) {
        width: myrem(314);
    }

}

.c-card05__image img {
    aspect-ratio: 345/204;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: myrem(15);
    @include mq(md) {
        aspect-ratio: 313/186;
    }
}

.c-card05__title {
    font-size: myrem(21);
    letter-spacing: 0.1em;
    font-weight: normal;
    margin-top: myrem(22);
    @include mq(md) {
        font-size: myrem(20);
        margin-top: myrem(18);
        margin-left: myrem(-2);
    }
}

.c-card05__text {
    font-size: myrem(14);
    line-height: calc(24 / 14);
    letter-spacing: 0.03em;
    margin-top: myrem(11);
    @include mq(md) {
        margin-top: myrem(7);
        margin-right: myrem(2);
    }
}

.c-card05__text--mt {
    margin-top: myrem(8);
    @include mq(md) {
        margin-top: myrem(7);
    }
}

.p-card05 {
    @include mq(md) {
        display: flex;
        flex-wrap: wrap;
        gap: myrem(40) myrem(28);
        justify-content: center;
    }
}

