@use "global" as *;

.p-gnavi {
    position: fixed;
    top: myrem(60);
    left: 0;
    width:100%;
    height: 100vh;
    z-index: 100;
    background-color: $white;
    transform: translateX(100%);
    transition: ease .5s;
    padding-top: myrem(38);
    @include mq(md) {
        position: relative;
        top: 0;
        left: auto;
        height: myrem(80);
        max-height: 80px;
        display: flex;
        align-items: center;
        transform: translateX(0);
        padding-top: 0;
        transition: none;
    }
}

.p-gnavi__inner {
    @include mq(md) {
        padding: 0;
        max-width: none;
    }
}

.p-gnavi__list {
    width: 100%;
    padding: 0 myrem(15);
    @include mq(md) {
        display: flex;
        flex-direction: row-reverse;
        padding: 0;
    }
}

// webボタン
.p-gnavi__item--web-button {
    background-color: $blue1;
    color: $white;
    border-radius: myrem(10);
    margin-bottom: myrem(8);
    @include mq(md) {
        border-radius: myrem(0);
        margin-bottom: 0;
        width: clamp(0px , 120 / 1400 * 100vw , 120px);
        height: myrem(80);
        box-sizing: border-box;
    }
}

.p-gnavi__item--web-button-wrapper {
    display: flex;
    justify-content: center;
    height: 100%;
    padding-top: myrem(20);
    padding-bottom: myrem(20);
    @include mq(md) {
        flex-direction: column;
        box-sizing: border-box;
    }
}

.p-gnavi__item--web-button-text {
    display: flex;
    font-size: myrem(22);
    letter-spacing: 0.1em;
    margin-right: myrem(8);
    @include mq(md) {
        font-size: clamp(10px , 18 / 1400 * 100vw, 18px);
        padding: 0;
        padding-left: clamp(0px , 22 / 1400 * 100vw, 22px);
    }
}

.p-gnavi__item--web-button-text::before {
    content: url("../images/header/webBtn.svg");
    width: myrem(30);
    height: myrem(32);
    margin-right: myrem(20);
    @include mq(md) {
        display: none;
    }
}

// tellボタン
.p-gnavi__item--tell-button {
    background-color: #EDF5F5;
    color: $black;
    border-radius: myrem(10);
    margin-bottom: myrem(18);
    @include mq(md) {
        border-radius: 0;
        margin-bottom: 0;
        box-sizing: border-box;
        width: clamp(0px ,262 / 1400 * 100vw, 262px);
        height: myrem(80);
    }
}

.p-gnavi__item--tell-button-inner {
    padding-top: myrem(3);
    padding-bottom: myrem(12);
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mq(md) {
        justify-content: center;
        align-items: flex-start;
        box-sizing: border-box;
        padding: 0;
    }
}

a[href^='tel:'] {
    @include mq(md) {
        pointer-events: none;
    }
}

.p-gnavi__item--tell-button-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mq(md) {
        align-items: initial;
        justify-content: center;
        height: 100%;
        padding-top: myrem(19);
        padding-left: clamp( 0px , 24 / 1400 * 100vw , 24px);
        text-align: right;
        gap: myrem(11);
    }
}

.p-gnavi__item--tell-button-common-text {
    font-size: myrem(26);
    line-height: calc(37/26);
    font-weight: 700;
    letter-spacing: 0.027em;
    @include mq(md) {
        font-size: clamp(10px ,26 / 1400 * 100vw, 26px);
        line-height: 0;
        letter-spacing: 0;
        padding-left: 0;
    }
}

.p-gnavi__item--tell-button-common-text::before {
    content: url("../images/header/tellBtn.svg");
    width: myrem(15);
    height: myrem(20);
    margin-right: myrem(8);
    @include mq(md) {
        content: "TEL.";
        font-size: clamp(10px , 16 / 1400 * 100vw, 16px);
        margin-right: 0;
    }
}

.p-gnavi__item--tell-button-sub-text {
    font-size: myrem(12);
    line-height: calc(17/12);
    font-weight: 400;
    letter-spacing: 0.04em;
    margin-top: myrem(1);
    margin-left: myrem(4);
    @include mq(md) {
        font-size: clamp(7px , 12 / 1400 * 100vw, 12px);
        font-weight: 400;
        letter-spacing: 0;
        margin-top: 0;
    }
}

// linktext
.p-gnavi__link-list {
    @include mq(md) {
        display: flex;
        align-items: center;
    }
}

.p-gnavi__link-list ul {
    @include mq(md) {
        position: relative;
        display: flex;
        align-items: center;
    }
}

.p-gnavi__item  {
    @include mq(md) {
        height: clamp(58.5px , 80 / 1400 * 100vw , 80px);
    }
}

.p-gnavi__item:nth-child(4)  {
    @include mq(md) {
        position: relative;
    }
}

.p-gnavi__item > a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    @include mq(md) {

    }
}

.p-gnavi__accordion-button-wrapper{
    display: flex;
    align-items: baseline;
    width: 100%;
    @include mq(md) {
    }
}

.p-gnavi__item-text {
    font-weight: 400;
    font-size: myrem(18);
    letter-spacing: 0.04em;
    padding-top: myrem(13);
    padding-bottom: myrem(11);
    padding-left: myrem(10);
    width: 100%;
    border-bottom: 1px solid $blue2 ;
    @include mq(md) {
        position: relative;
        font-size: clamp(9px , 16 / 1400 * 100vw, 16px);
        letter-spacing: 0.1em;
        padding-top: clamp(23px , 28 / 1400 * 100vw , 28px);
        padding-bottom: clamp(22px , 28 / 1400 * 100vw , 28px);
        padding-left: clamp(0px , 26.5 / 1400 * 100vw, 26.5px);
        padding-right: clamp(0px , 26.5 / 1400 * 100vw, 26.5px);
        white-space: nowrap;
        border-bottom: 0;
        cursor: pointer;
        transition: all 0.3s ease;
    }
}

.p-gnavi__item-text2, 
.p-gnavi__item-text3 {
    padding-top: myrem(11);
    @include mq(md) {
        padding-top: clamp(23px , 28 / 1400 * 100vw , 28px);
    }
}

.p-gnavi__item-text4 {
    padding-top: myrem(13);
    padding-right: myrem(30);
    @include mq(md) {
        padding-top: clamp(23px , 28 / 1400 * 100vw , 28px);
        padding-right: clamp(0px, 2.1428571429vw, 30px);
    }
}

.p-gnavi__item-text::before {
    @include mq(md) {
        position: absolute;
        content: "";
        background-color: #484848;
        width: 1px;
        height: myrem(16);
        top: 44%;
        left: 0;
    }
}

.p-gnavi__item:nth-child(1) .p-gnavi__item-text::before {
    @include mq(md) {
        width: 0;
        height: 0;
    }
}

.border::before {
    @include mq(md) {
        width: 0;
        height: 0;
    }
}

.p-gnavi__item-text:hover {
    @include mq(md) {
        color: $blue1;
        font-weight: bold;
    }
}

.p-gnavi__item button{
    font-size: myrem(0);
    @include mq(md) {
        
    }
}

.p-gnavi__drop-down-button {
    position: absolute;
    display: block;
    font-size: 0;
    top: myrem(392);
    right: myrem(30);
    @include mq(md) {
        display: none;
    }
}

.p-gnavi__item-span {
    position: relative;
    display: inline-block;
    transition: all 0.3s ease;
    @include mq(md) {  
    }
}

.p-gnavi__item-span::before {
    content: '';
    width: myrem(12);
    height: myrem(12);
    border: 0;
    border-top: solid 2px $blue1;
    border-right: solid 2px $blue1;
    transform: rotate(135deg);
    position: absolute;
    top: myrem(-10);
    left: myrem(-25);
    margin: auto;
    transition: all 0.3s ease ;
    @include mq(md) {
        display: none;
    }
}

.p-gnavi__item-span.flipped::before {
    transition: all 0.3s ease;
    transform: rotate(315deg); /* 折り返す動き */
}

// accordion

.p-gnavi__accordion-list-wrapper {
    position: absolute;
    width: 100%;
    top: myrem(412);
    left: myrem(31);
    display: none;
    transition: 0.3s all ease;
    @include mq(md) {
        display: block;
        background: $white;
        flex-direction: column;
        width: clamp(90px , 163 / 1400 * 100vw , 163px);
        height: clamp(108px ,  187 / 1400 * 100vw , 187px);
        box-sizing: border-box;
        top: myrem(72);
        left: myrem(-2);
        border-radius: 0 0 myrem(10) myrem(10);
        z-index: -1;
        transition: all 0.3s ease;
        opacity: 0.8;
        transform: scaleY(0);/*デフォルトでは非表示の状態にしておく*/
        transform-origin: center top;
    }
}

// spのアニメーション
.p-gnavi__accordion-list-wrapper.open {
    display: block; 
    transition: all 0.3s ease;
    @include mq(md) {

    }
}
// pcのアニメーション
.p-gnavi__item:nth-child(4):hover .p-gnavi__accordion-list-wrapper{
    @include mq(md) {
        transform: scaleY(1);
        transition: all 0.3s ease;
    }
}

.p-gnavi__accordion-list {
    @include mq(md) {
        display: flex;
        flex-direction: column;
    }
}

.p-gnavi__accordion-item {
    position: relative;
    display: block;
    width: 100%;
    font-size: myrem(15);
    letter-spacing: 0.04em;
    font-weight: 400;
    color: $black;
    white-space: nowrap;
    @include mq(md) {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: clamp(10px , 16 / 1400 * 100vw, 16px);
        letter-spacing: 0.1em;
        transition: all 0.3s ease;
    }
}

.p-gnavi__accordion-item a {
    display: flex;
    padding-top: myrem(10);
    padding-left: myrem(45);
    padding-bottom: myrem(12);
    @include mq(md) {
        display: block;
        padding-top: clamp(0px , 22 / 1400 * 100vw , 22px);
        padding-right: clamp(0px , 47 / 1400 * 100vw , 47px);
        padding-bottom: clamp(0px , 22 / 1400 * 100vw , 22px);
        padding-left: clamp(0px , 47 / 1400 * 100vw , 47px);
    }
}

.p-gnavi__accordion-item:nth-child(2) a {
    @include mq(md) {
        padding-top: clamp(0px , 13 / 1400 * 100vw , 13px);
        padding-bottom: clamp(0px , 15 / 1400 * 100vw , 15px);
    }
}

.p-gnavi__accordion-item:nth-child(3) a {
    @include mq(md) {
        padding-top: clamp(0px , 20 / 1400 * 100vw , 20px);
        padding-bottom: clamp(0px , 22 / 1400 * 100vw , 22px);
    }
}

.p-gnavi__accordion-item a:hover {
    @include mq(md) {
        color: $blue1;
        font-weight: bold;
    }
}

// spの疑似要素
.p-gnavi__accordion-item span {
    position: relative;
    display: inline-flex;
    @include mq(md) {
        display: none;
    }
}

.p-gnavi__accordion-item span::before {
    content: '';
    width: myrem(16);
    height: myrem(1);
    border: 0;
    border-top: solid 1px $blue1;
    position: absolute;
    top: myrem(12);
    left: myrem(-26);
    @include mq(md) {
    }
}
// pcのaccordionのborder疑似要素
.pc-border {
    @include mq(md) {
        position: relative;
    }
}

.pc-border::before {
    @include mq(md) {
        position: absolute;
        content: '';
        top: myrem(0);
        width: clamp(0px , 134 / 1400 * 100vw , 134px);
        height: myrem(1);
        background-color: $black;
        opacity: 0.3;
    }
}