@use "global" as *;

body {
    counter-reset: number number-m;
}

.c-card03 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: $blue2;
    aspect-ratio: 152/153;
    border-radius: 50%;
    max-width: 100%;
    width: 40.53vw;
    height: auto;
    position: relative;
    @include mq(md) {
        width: myrem(180);
        max-width: myrem(180);
        aspect-ratio: 180/180;
        gap: myrem(26);
    }
}

.p-card03 > .c-card03:not(:first-child)::before,
.p-card03:not(:first-child) > .c-card03::before {
    content: '';
    position: absolute;
    width: 3.466vw;
    height: 6.66vw;
    background: $blue2;
    clip-path: polygon(0 0, 0% 100%, 100% 50%);
    left: -4vw;
    @include mq(md) {
        width: myrem(16);
        height: myrem(29);
        left: myrem(-20);
    }
}

.c-card03::after {
    counter-increment: number 1;
    content: counter(number, decimal-leading-zero) "";
    font-size: 4.266vw;
    font-weight: bold;
    letter-spacing: 0;
    color: $blue1;
    position: absolute;
    background: $blue3;
    width: 11.2vw;
    height: 10.93vw;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0.533vw;
    left: 0;
    @include mq(md) {
        width: myrem(50);
        height: myrem(50);
        font-size: myrem(20);
        top: myrem(-2);
    }
}



.c-card03__contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    @include mq(md) {

    }
}

// 画像

.c-card03__image1 {
    width: 16vw;
    margin-bottom: 2.66vw;
    @include mq(md) {
        width: myrem(72);
        margin-top: myrem(8);
        margin-bottom: myrem(9);
    }
}

.c-card03__image1 img {
    aspect-ratio: 60/53;
    width: 100%;
    height: 100%;
    object-fit: contain;
    @include mq(md) {
        aspect-ratio:  72/63;
    }
}

.c-card03__image2 {
    width: 17.6vw;
    margin-bottom: 5.33vw;
    @include mq(md) {
        width: myrem(78);
        margin-top: myrem(10);
        margin-bottom: myrem(20);
    }
}

.c-card03__image2 img {
    aspect-ratio: 66/50;
    width: 100%;
    height: 100%;
    object-fit: contain;
    @include mq(md) {
        aspect-ratio:  78/60;
    }
}

.c-card03__image3 {
    width: 12.53vw;
    margin-bottom: 1.33vw;
    @include mq(md) {
        width: myrem(56);
        margin-top: myrem(3);
        margin-bottom: myrem(16);
    }
}

.c-card03__image3 img {
    aspect-ratio: 47/63;
    width: 100%;
    height: 100%;
    object-fit: contain;
    @include mq(md) {
        aspect-ratio:  56/75;
    }
}

.c-card03__image4 {
    width: 13.86vw;
    margin-bottom: 2.66vw;
    @include mq(md) {
        width: myrem(62);
        margin-top: myrem(8);
        margin-bottom: myrem(9);
    }
}

.c-card03__image4 img {
    aspect-ratio: 52/54;
    width: 100%;
    height: 100%;
    object-fit: contain;
    @include mq(md) {
        aspect-ratio:  62/64;
    }
}

// 丸の中の文章
.c-card03__text {
    font-size: 2.933vw;
    line-height: calc(16 / 11);
    font-weight: 600;
    letter-spacing: 0.1em;
    text-align: center;
    color: $white;
    @include mq(md) {
        font-size: myrem(15);
        line-height: calc(21/15);
    }

}

.p-card03 {
    display: flex;
    flex-wrap: wrap;
    gap: 8vw 6.4vw;
    @include mq(md) {
        display: contents;
        flex-wrap: initial;
        gap: 0;
        margin-top: myrem(113);
    }
}